<script>
  import { onMount } from "svelte";

  import {
    nickname,
    nicknames,
    fetchNicknames, refresh_access_token,
    mode,
  } from "../store.js";
  let player;
  let playerscore;
  function handle_player_change(event) {
    player = event.target.value;
  }
  function handle_playerscore_change(event) {
    playerscore = event.target.value;
  }

  onMount(async () => {
    fetchNicknames($mode); //  
  });

  async function sendRequest(endpoint, method, data = null) {
  let checkjwt = localStorage.getItem("d2rpvpjwtToken");

  const sendFetchRequest = async () => {
    const options = {
      method: method,
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${checkjwt}`,
      },
    };

    if (data) {
      options.body = JSON.stringify(data);
    }

    const response = await fetch(endpoint, options);
    return response;
  };

  let response = await sendFetchRequest();

  if (response.status === 403) {
    await refresh_access_token();
    checkjwt = localStorage.getItem("d2rpvpjwtToken");
    response = await sendFetchRequest(); //     
  }

  return response;
}

async function submit_bonus_score() {
  const data = { player: player, playerScore: playerscore };

  try {
    const response = await sendRequest("/submit-admin-score", "POST", data);

    if (response && response.status === 200) {
      alert("  ");
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    alert("  :", error);
  }
}

async function score_reset() {
  const userResponse = confirm(
    "   . ?"
  );

  if (userResponse) {
    try {
      const response = await sendRequest("/reset-rank", "POST");

      if (response && response.status === 200) {
        alert(" ");
      } else {
        alert(` : ${response.status}`);
      }
    } catch (error) {
      alert("  :", error);
    }
  }
}
</script>

<div class="main_data">
  {#if $nickname === "admin"}
    <div class="left">
         
      <select
        bind:value={player}
        on:change={handle_player_change}
        class="namewidth"
      >
        {#each $nicknames as option}
          <option value={option}>{option}</option>
        {/each}
      </select>
      <input
        type="number"
        bind:value={playerscore}
        on:change={handle_playerscore_change}
        placeholder=""
        class="scorewidth"
      />
      <button on:click={() => submit_bonus_score()}> </button>
      <br />
      <button on:click={() => score_reset()}>  </button>
    </div>
  {:else}
    <div class="left"> .</div>
  {/if}
</div>
