<script>
  import { onMount } from "svelte";

  import {
    nicknames,
    fetchNicknames,
    nickname,
    mode,
  } from "./store.js";

  let nicknameexceptme = [];
  let myScore = 0;
  let winner;
  let winnerScore = 5;

  onMount(async () => {
    fetchNicknames($mode); //  
    nicknameexceptme = $nicknames.filter((item) => item !== $nickname);
  });

  async function submitrecord() {
  const data = {
    winner: winner,
    winnerScore: winnerScore,
    myScore: myScore,
  };
  let checkjwt = localStorage.getItem("d2rpvpjwtToken");

  if (!checkjwt) {
    alert(" ");
    return;
  }

  if (myScore > 4 || myScore < 0) {
    alert("  ");
    return;
  }

  const sendSubmitRequest = async () => {
    const response = await fetch("/submitrecord", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${checkjwt}`,
      },
      body: JSON.stringify(data),
    });

    return response;
  };

  try {
    let response = await sendSubmitRequest();

    if (response.ok) {
      alert("  ");
    } else if (response.status === 403) {
      await refresh_access_token();
      checkjwt = localStorage.getItem("d2rpvpjwtToken");

      response = await sendSubmitRequest(); //     

      if (response.ok) {
        alert("  ");
      } else {
        alert(` : ${response.status}`);
      }
    } else {
      alert(` : ${response.status}`);
    }
  } catch (error) {
    console.error(" :", error);
    alert(" : " + error.message);
  }
}

  function handle_winner_change(event) {
    winner = event.target.value;
  }
</script>

<div class="left">
   
  <select
    bind:value={winner}
    on:change={handle_winner_change}
    class="namewidth"
  >
    {#each nicknameexceptme as option}
      <option value={option}>{option}</option>
    {/each}
  </select>
    
  <input type="number" class="scorewidth" bind:value={myScore} />
  <button on:click={() => submitrecord()}></button>
</div>
